import Vue from 'vue'
import Element from 'element-ui'
import scroll from 'vue-seamless-scroll'
import Icon from 'vue-svg-icon/Icon.vue'
import '@/theme/element-#41B883/index.css'
import '@/assets/styl/index'
import '@/mock'

import App from '@/App'
import { router } from '@/router'
import '@/router/routerControl'
import store from '@/store'

import i18n from '@/lang'
import axios from './axios'
import echarts from 'echarts'
import gojs from 'gojs'
import wholeMethods from '@/utils/wholeMethods'
Vue.component('icon', Icon)

Vue.prototype.$axios = axios
Vue.prototype.$echarts = echarts
Vue.prototype.$go = gojs
// 注册common方法
Object.keys(wholeMethods).forEach(funcName => {
  Vue.prototype[funcName] = wholeMethods[funcName]
})

Vue.use(scroll)
Vue.use(Element, {
  size: 'medium',
  i18n: (key, value) => i18n.t(key, value)
})

Vue.config.productionTip = false

new Vue({
  el: '#app',
  router,
  store,
  i18n,
  components: { App },
  template: '<App/>'
})
