import axios from 'axios'
import { Message, Loading } from 'element-ui'
import router from '../router'

// axios.defaults.baseURL = 'https://tcc.taobao.com/cc'
axios.defaults.timeout = 5000
axios.defaults.headers.post['Content-Type'] = 'application/json'

let loading = null

// http request 拦截器
axios.interceptors.request.use(
  config => {
    loading = Loading.service({
      lock: true,
      text: 'Loading...',
      background: 'rgba(0, 0, 0, 0.7)'
    })
    return config
  },
  error => {
    return Promise.reject(error)
  }
)

const showErrorMsg = err => {
  Message({
    showClose: true,
    message: err.data ? err.data : err.statusText,
    type: 'error'
  })
}

// http response 拦截器
axios.interceptors.response.use(
  response => {
    loading.close()
    return response
  },
  error => {
    if (error.response) {
      switch (error.response.status) {
        case 401:
          Message({
            type: 'error',
            message: '登录失效！请重新登录'
          })
          router.push('/login')
          break
        case 404:
          Message({
            showClose: true,
            message: error.response.statusText,
            type: 'error'
          })
          break
        case 500:
          showErrorMsg(error.response)
          break
        default:
          break
      }
    }
    loading.close()
    return Promise.reject(error && error.response)
  }
)

export default axios
