export default {
  commonFunc: {
    /*
         * element时间控件默认获取的UTC时间转换
         * return 中国标准时间 */
    elementDateFormat(time) {
      let formatTime = new Date(time)
      return new Date(formatTime.setHours(formatTime.getHours() + 8))
    }
  },
  /*
     * 制作者：乔杰
     * return 中国标准时间 */
  conmmonMethods: {
    /*
         * 获得当前时间
         * return 中国标准时间 */
    getNowFormatDate() {
      var date = new Date()
      var seperator1 = '/'
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      var strDate = date.getDate()
      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      var currentdate = year + seperator1 + month + seperator1 + strDate
      return currentdate
    }
  },
  /*
     * 清空对象
     * return 中国标准时间 */
  make_clear(data) {
    if (data) {
      for (let item in data) {
        data[item] = ''
      }
    }
  },
  /* 乔杰
     * 分拆数组
     * return 新数组 */
  getNewArray(array, a) {
    if (Array.isArray(array)) {
      let newArray = array.map(function(item) {
        return item[a]
      })
      return newArray
    }
  },
  /* 乔杰
     * element - uiMessageBox 弹框
     * return 链式调用 */
  makePopBox(words) {
    // this.makePopBox('启动').then(() => {})
    let _this = this
    return new Promise(function(resolve) {
      _this
        .$confirm('是否' + words, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        })
        .then(() => {
          resolve()
        })
        .catch(() => {
          _this.$message({
            type: 'info',
            message: '已取消该操作'
          })
        })
    })
  },
  /* 乔杰
     * 时间
     * return 时间差 */
  makeDate({ startTs, endTs }) {
    var dateOne = new Date(startTs)
    var dateTwo = new Date(endTs)
    let ck = dateTwo.getTime() - dateOne.getTime()
    return ck
  },
  // 时间格式化处理
  dateFtt(fmt, date) {
    const o = {
      'M+': date.getMonth() + 1, // 月份
      'd+': date.getDate(), // 日
      'h+': date.getHours(), // 小时
      'm+': date.getMinutes(), // 分
      's+': date.getSeconds(), // 秒
      'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
      S: date.getMilliseconds() // 毫秒
    }
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        (date.getFullYear() + '').substr(4 - RegExp.$1.length)
      )
    }
    for (let k in o) {
      if (new RegExp('(' + k + ')').test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1,
          RegExp.$1.length === 1
            ? o[k]
            : ('00' + o[k]).substr(('' + o[k]).length)
        )
      }
    }
    return fmt
  }
}
